<template><div style="position: relative">

<div id="colorwheel" :style="cw">
	<div id="colorwheel_1" :style="cw1">
		<span v-for="(n,i) in 360" :style="cw1_span(i)"></span>
	</div>
	<div id="colorwheel_2" :style="cw2">
		<span v-for="(n,i) in 60" :style="cw2_span(i)" @click="hue = i"> </span>
	</div>
	<div id="colorwheel_3" :style="cw3">
		<div v-for="(n,i) in 60" :style="style_numerals(i)"> {{ pad(i,2) }} </div>
	</div>
</div>

<div  :style="cont_width">

	<div v-for="lum_or_sat in ['lum','sat']">
		<div class="d-flex justify-space-between px-4 text-h5 text-md-h4">
			<div> {{ lum_or_sat == 'lum' ? 'lau' : 'bil' }} </div>
			<div> {{ lum_or_sat == 'lum' ? 'lai' : 'bul' }} </div>
		</div>

		<div class="mt-5" style="position:relative">
			<div id="lum" class="d-flex" :style="cont_height" >
				<div v-if="hue !== null" class="flex" v-for="(n,i) in 200" :style="bgc(i, lum_or_sat)"></div>
			</div>
			<div style="height: 100%" class="k d-flex justify-center">
				<div class="d-flex" style="width: 95%;">
					<div v-for="(n,i) in 19" v-if="hue !== null" class="flex L2" @click="lum_or_sat == 'lum' ? lum = n*5 : sat = n*5 "> </div>
				</div>
			</div>
			<div style="height: 0px" class="k d-flex justify-center">
				<div class="kc d-flex">
					<div class="d-flex justify-center" v-for="(n,i) in 19"> {{ Math.abs(i-9) }} </div>
				</div>
			</div>
		</div>
	</div>

	<div v-if="hue !== null" class="d-flex flex-column align-center justify-center pt-5">
		<p>The resulting color:</p>
		<div class="text-h4 text-md-h3"> <nao nodecor pronounce :o="color_name_digits" /> </div>
		<div id="colorbox" :style="cb_style"></div>
		<div class="text-h5 text-md-h3"> {{ color_name_words }} </div>
	</div>

</div>

</div></template>

<script>

import nao from '@/components/Naoword'
// <p class="text-center text-h5">Luminosity</p>
// <div style="width: 780px; height: 780px; position: static"> </div>

export default {
	components: {nao},
	props: ['size'],
	data() {
		return {
			hue: null,
			lum: null,
			sat: null
		}
	},
	computed: {
		cw() {
			return `width: ${780*this.size}px; height: ${780*this.size}px;`;
		},
		cw1() {
			return `top: ${385*this.size}px; left: ${540*this.size}px;`;
		},
		cw2() {
			return 	`top: ${377*this.size}px; left: ${540*this.size}px;`;
		},
		cw3() {
			return `top: ${390*this.size}px; left: ${390*this.size}px;`
		},
		cont_size(){
			return `width: ${600*this.size}px; height: ${50*this.size}px`;
		},
		cont_height(){
			return `height: ${50*this.size}px`;
		},
		cont_width(){
			return `width: ${760*this.size}px`;
		},
		cb_style() {
			return `background-color: hsl(${this.hue*6}, ${this.sat || 100}%, ${this.lum || 50}%)`;
		},
		color_name_digits() {
			return this.$nao.HSL_to_Nao(this.hue, this.lum, this.sat);
		},
		color_name_words() {
			return this.$nao.replace_all_numbers_to_words(this.color_name_digits);
		}
	},
	methods: {
		hue_clicked(i) {
			console.log("You clicked ha" + i);
			this.hue = i;
		},
		style_numerals(n) {
			let a = (n/30 - 0.5)*Math.PI;
			let top = Math.round(370*this.size*Math.sin(a)) + 'px';
			let left = Math.round(370*this.size*Math.cos(a)) + 'px' ;
			return 'top:' + top + '; left: ' + left + (n % 5 == 0 ? `; font-size: ${30*this.size}px !important; font-weight: bold` : '; color:grey');
		},
		pad(x, n) {
			x += '';
			while(x.length < n) x = '0' + x;
			return x;
		},
		cw1_span(i) {
			return `width: ${200*this.size}px; height: ${8}px; -webkit-transform-origin: ${-150*this.size}px; background-color: hsl(${i+90}, 100%, 50%); transform: rotate(${i}deg)`
		},
		cw2_span(i) {
			return `width: ${200*this.size}px; height: ${28*this.size}px; -webkit-transform-origin: ${-150*this.size}px; transform: rotate(${-90+6*i}deg)`
		},
		bgc(i, lum_or_sat) {
			let t = lum_or_sat == 'lum' ? '100%,' + i/2 : i/2 + '%, 50'
			return 'background-color: hsl(' + this.hue*6 + ',' + t + '%)';
		}
	},
	created() {

	}
}
</script>

<style scoped> 

#rescol {
	font-size: 40px;
	padding: 10px;
}

.big {
	font-size: 30px !important;
}

#colorwheel {
	text-align: center;
	font-size: 40px;
}

#lum, #sat {
	border: 3px solid gray;
	position: relative;
}

.L1 {
	position: absolute;
	width: 3px;
	height: 50px;
}

.L2 {
	cursor: pointer;
}

.L3 {
	cursor: pointer;
	position: absolute;
	width: 30px;
	height: 30px;
	top: -30px;
	text-align: center;
	font-size: 14px;
}

.L2:hover, .L2:active {
	border: 2px solid black;
}

#colorwheel * {
	position: absolute;
}

#colorwheel_2 > * {
	cursor: pointer;
	xposition: absolute;
	text-align: right;
}

#colorwheel_3 > * {
	position: absolute;
	width: 2px;
	height: 2px;
	font-size: 10px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}

#colorwheel_2 > *:hover, #colorwheel_2 > *:active {
	border: 2px solid black;
}

.k {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
}

.kc {
	width: 95%;
	height: 20px;
	transform: translateY(-20px);
}

.kc > div {
	flex: 1 1 0px;
}

#colorbox {
	width: 200px;
	height: 100px;
	border-radius: 30px;
	border: 1px solid black;
}

</style>