<template><div class="b_naoword i_english">

<div v-if="cur_lesson == 'alphabet-and-pronunciation'">
	<div :class="$css.title"> Алфавит и произношение </div>

	<p> Основной алфавит Nao состоит из: </p>
	<p> 5 гласных: <b>a i u e o </b> </p>
	<p> 14 согласных: <b>p t k b d g s c h f l m n j </b> </p>
	<p> Ещё 4 буквы используются только в заимствованиях: <b>y z v r  </b> </p>

	<p> Хорошая новость заключается в том, что вы легко догадаетесь, как произносится большинство из них. Так что давайте просто посмотрим на самые неочевидные буквы: </p>

	<v-simple-table class="mb-4">
	<template v-slot:default>
	  <thead>
	    <tr>
	      <th v-for="cell in ['Letter', 'Pronunciation', 'Example']" > {{ cell }} </th>
	    </tr>
	  </thead>
	  <tbody>
	    <tr
	      v-for="row in [
	      	['c', 'как <i>ш</i>', 'gocu'],
	      	['j', 'как мягкая <i>дж</i>', 'joi'],
	      	['l', 'полумягкая <i>л</i>', 'lapal'],
	      	['r', 'как русская <i>р</i>', '\'rus'],
	      	['y', 'как первая <i>о</i> в слове х<i>о</i>рошо)', '\'kentyki']
	      ]"
	      :key="row[0]"
	    >
	      <td> <b> {{ row[0] }} </b> </td>
	      <td v-html="row[1]"> </td>
	      <td > <nao pronounce :o="row[2]"/> </td>
	    </tr>
	  </tbody>
	</template>
	</v-simple-table>

	<p> Остальные буквы произносятся так, как вы ожидаете. Также все слова и фразы в учебнике имеют звуковое сопровождение. </p>

	<p> Каждой букве соответствует один звук. Все слова читаются так, как пишутся. </p>

	<p> Гласные <b>i</b> и <b>u</b> в сочетании с другими гласными не образуют слога. Они произносятся безударно и кратко. Например: </p>

	<div class="pl-6 mb-4">
		<div> <nao pronounce o="joi"/> звучит как <i>джой</i> </div>
		<div> <nao pronounce o="kau"/> звучит как <i>кау</i> </div>
		<div> <nao pronounce o="niu"/> звучит как <i>нью</i> </div>
	</div>

	<p> В базовых словах ударение падает на первый слог. Например: </p>

	<v-flex d-flex flex-wrap mb-4 pl-7>
	<v-flex v-for="p in [
		{'f<i>a</i>ia': 'гореть', 'ki<i>o</i>': 'брат/сестра' },
		{'\'k<i>a</i>lifornia': 'Калифорния', '\'pu<i>e</i>rto-riko': 'Пуэрто-Рико' },
	]" pa-2>
		<div v-for="w in Object.keys(p)">
			<span v-html="w"> </span>
			(= <span> {{ p[w] }}</span> )
		</div>
	</v-flex>
	</v-flex>

	<p> В сочетаниях ударение семантическое, как правило оно падает на последнее базовое слово. </p>

	<v-flex d-flex flex-wrap mb-4 pl-7>
	<v-flex v-for="p in [
		{'madisako<i>hom</i>': 'школа', 'ladi<i>foi</i>': 'ускорение' },
		{'soinie<i>nai</i>': 'лето', 'kanedu<i>faia</i>': 'огнетушитель' },
	]" pa-2>
		<div v-for="w in Object.keys(p)">
			<span v-html="w"> </span>
			(= <span> {{ p[w] }}</span> )
		</div>
	</v-flex>
	</v-flex>
</div>


<div v-if="cur_lesson == 'phrases'">
	<div :class="$css.title"> Фразы </div>

	<p> Вот некоторые фразы и предложения, которые могут дать вам общее представление о языке и о том, как он работает. </p>

	<PAT :pairs="phrases"/>
</div>


<div v-else-if="cur_lesson == 'simple-sentences'">

	<div :class="$css.title"> Строим простые предложения </div>

	<p> В Nao очень легко строить предложения - в нём нет склонений, спряжений, а порядок слов похож на прямой порядок слов в русском языке. </p>

	<PAT :pairs="{'me eat/drink rice': 'Я ем рис.'}"/>

	<p> Как можно заметить, порядок слов такой: Подлежащее + Сказуемое + Объект. По умолчанию, глагол не стоит ни в каком времени, поэтому вышеупомянутое предложение может обозначать
	<span class="english_word"> я ел рис</span>
		или
	<span class="english_word"> я ем рис </span>
		или
	<span class="english_word"> я буду есть рис </span>
	и так далее. В этом случается время просто угадывается по контексту. Теперь давайте взглянем на эти предложения:
	</p>

	<PAT :pairs="{'dog see/look me' : 'Собака смотрит на меня.', '\'sara married \'piter' : 'Сара замужем за Питером.'}"/>

	<p> В русском языке мы часто используем предлоги с глаголами, но в Nao предлоги зачастую уже "встроены" в глагол. Также, в Nao по умолчанию существительное может быть как единственного, так и множественного числа, поэтому
	<span :class="$css.naoword"> dog </span> может означать как одну собаку, так и несколько собак.
	</p>

	<p> Чтобы образовать отрицание, просто поставьте <nao n="not"/> перед глаголом: </p>

	<PAT :pairs="{'\'jon not eat/drink rice' : 'Джон не ест рис.'}"/>

	<p> Чтобы превратить предложение в вопрос, необходимо поставить частицу <nao pronounce n="QUESTION"/> в начало предложения: </p>

	<PAT :pairs="{'QUESTION you happy' : 'Ты рад?'}"/>

	<p>
	Обратите внимание:
	<br>1. Nao не использует заглавные буквы, чтобы минимизировать количество разных символов в языке.
	<br>2. Предложения отделяются друг от друга тире (−) или дефисом (-), потому что без заглавных букв точка слишком мала и незаметна для отделения предложений.
	При написании текста, начинать первое предложение с тире и заканчивать последнее с тире является необязательным.
	<br>3. Nao не использует знаки пунктуации в конце предложения типа вопросительного и восклицательного знаков, троеточия и так далее. 
	</p>

	<PAT :pairs="{'− happy-meet − you named what −' : 'Здравствуйте. Как Вас зовут?'}"/>

	<p> Стоит подметить, что по умолчанию вся речь в Nao является нейтральной по стилю, нет разделения на вежливую и фамильярную речь. Поэтому вышеупомянутое предложение можно перевести и таким образом:</p>

	<PAT :pairs="{'− happy-meet − you named what −' : 'Привет. Как тебя зовут?'}"/>

	<p> Вопросительный знак в Nao по просту не нужен, потому что каждое вопросительное предложение содержит <nao n="QUESTION"/>  и/или  <nao n="what"/>. Другие знаки не используются, потому что правила их применения отличаются в разных языках мира. </p>

</div>


<div v-else-if="cur_lesson == 'opposite'">

	<div :class="$css.title"> Образование антонимов </div>

	<p> Одна из самых мощных особенностей Nao это образование слов с противоположным значением - необходимо просто прочередовать 
	<span :class="$css.naoword"> i / u </span>. Например: </p>

	<PAT :little_columns="[
		{'warm/hot': 'тёплый/горячий', 'cold': 'холодный' },
		{'clean': 'чистый', 'dirty': 'грязный' },
		{'previous': 'предыдущий', 'next': 'следующий' }
	]" />

	<p> Обратите внимание, что для образования антонима чередуемая гласная <span :class="$css.naoword"> i / u </span> должна быть последней гласной в слове. Это значит, что, например, слова <nao o="dia"/> и <nao o="dua"/> не могут быть антонимами.

	<p> Также важно подметить, что некоторые слова не имеют антонимов чисто по смыслу. Например, 
		<span :class="$css.naoword"> maus </span> (мышь) и <span :class="$css.naoword"> mais </span> (кукуруза)
	разумеется не являются антонимами, потому что по их значению они не могут иметь никаких антонимов. </p>

	<p> Этот трюк работает также и на сочетаниях: </p>

	<PAT :little_columns="[
		{'color-completely-light': 'белый', 'color-completely-dark': 'чёрный'},
		{'day-previous': 'вчера', 'day-next': 'завтра' },
		{'cause-in_state-deserve-good': 'наградить', 'cause-in_state-deserve-bad': 'наказать' },
		{'part-year-warm/hot': 'лето', 'part-year-cold': 'зима' },
	]" />


	<div :class="$css.title"> Противоположность и отрицание </div>

	<p> Некоторые новички путают противоположность с обычным отрицанием. В реальности это две разные вещи. Давайте посмотрим на три возможные ситуации:</p>

	<v-simple-table class="mb-4">
	<template v-slot:default>
	  <thead>
	    <tr>
	      <th v-for="cell in ['Original word', 'Opposite', 'Negation']" class="text-left"> {{ cell }} </th>
	    </tr>
	  </thead>
	  <tbody>
	    <tr
	      v-for="(row,index) in [
			['dai = справа', 'dau = слева', 'nodai = не справа'],
			['bai = купить', 'bau = продать', 'nobai = не купить'],
			['pau = родитель', 'pai = ребёнок', 'nopau = не родитель'],
			['li = хороший', 'lu = плохой', 'noli = не хороший'],
			['bul = сильный', 'bil = слабый', 'nobul = не сильный'],
			['dei = истинный', 'deu = ложный', 'nodei = не истинный'],
			['mai = разрешено', 'mau = запрещено', 'nomai = не разрешено'],
		]"
	      :key="row[0]"
	      :style="'background-color:' + (index < 3 ? '#a1c8fc' : index < 5 ? '#fcf7a1' : '#a4fca1')"
	    >
	      <td v-for="cell in row"> <b>{{ cell.split('=')[0] }}</b> <br v-if="$vuetify.breakpoint.xs"><span v-else>=</span> {{ cell.split('=')[1] }} </td>
	    </tr>
	  </tbody>
	</template>
	</v-simple-table>

	<p>
		<div>синий – антоним и отрицание совершенно разные </div>
		<div>жёлтый – антоним и отрицание похожи, но не идентичны</div>
		<div>зелёный – антоним и отрцание идентичны по значению</div>
	</p>

	<p> Вы можете найти подробные правила образования антонимов в Полном Списке Правил II.8 </p>
</div>

<div v-else-if="cur_lesson == 'word-structure'">
	<div :class="$css.title"> Структура слова </div>
	
	<p> В Nao, каждое слово - это последовательность элементарных слов. Структура элемента: <i>согласная</i> + <i>гласная/гласные</i> + (возможно) <i>согласная</i>. Например: </p>

	<p>
		<div v-for="w in ['sudaban', 'henodutua', 'pottomfes']">
			<nao pronounce :o="w"/> = <span v-for="(el,index) in $nao.disassemble(w)"> <span v-if="index != 0"> + </span> <nao :o="el"> </nao> </span>
		</div>
	</p>

	<p> Поэтому вы всегда можете с лёгкостью определить структуру слова Nao со 100% точностью. А если вы знаете значения элементов и грамматику, вы всегда можете понять значение сочетания. Наведите на слово, чтобы увидеть их значения: </p>

	<p>
		<div v-for="w in ['bukan', 'dagocua']">
			<nao pronounce :o="w"/> =
			<span v-for="(el,index) in $nao.disassemble(w)">
				<span v-if="index != 0"> + </span> <nao :o="el"> </nao>
			</span>
			= {{ $nao.tr_from_Nao(w).main_tr }}
		</div>
	</p>

	<p> В Nao вы можете сами конструировать свои слова, но вы обязаны строго следовать грамматическим правилам. В этом учебнике мы шаг за шагом научимся это делать. Но если вы не хотите строить никакие слова, вы можете просто искать их в словаре. </p>

	<div :class="$css.title"> Апостроф </div>

	<p> Базовые слова с нестандартной структурой должны быть промаркированы апострофом. Также все имена собственные (напр. имена людей, названия городов, и т.д.) вне зависимости от их структуры пишутся с апострофом.</p>

	<PAT :little_columns="[
		{'\'inet': 'интернет', '\'kanin': 'кролик', '\'banan': 'банан'},
		{'\'tom': 'Том', '\'maiami': 'Маями', '\'basketbol': 'баскетбол'},
	]" />
</div>

<div v-else-if="cur_lesson == 'main-grammar-rule'">

	<div :class="$css.title"> Главное правило грамматики </div>

	<p> Порядок слов в Nao определяется одним главным правилом: </p>
	<v-alert
	  color="blue-grey"
      dark
      dense
      :icon="$mdi.School"
      prominent
	> 
		Описание идёт после описуемого </v-alert>
	<p> Это правило очень важно, потому что оно определяет порядок слов во всём языке, а также порядок элементов в сочетаниях. В лингвистике такое свойство называется строгим правым ветвлением. Давайте посмотрим, что это значит на практике: </p>

	<p> 1. Прилагательные идут после существительных (потому что прилагательные описывают существительные):</p>

	<PAT :pairs="{'building big' : 'большой дом', 'female PASSIVE-like-see/look':'красивая девушка' }"/>

	<p> 2. Сочетание <i>сущ<sub>1</sub> сущ<sub>2</sub></i> означает <i>сущ<sub>1</sub> (родительный падеж) сущ<sub>2</sub></i> (потому что второе существительное описывает первое существительное): </p>

	<PAT :pairs="{'year act_of-begin-live' : 'год рождения', 'person-control group-person-work':'начальник компании' }"/>

	<p> 3. Сочетание <i>сущ глаг</i> означает <i>сущ, которое делает глаг</i>: </p>

	<PAT :pairs="{'person work' : 'человек, который работает', 'someone/something cover':'вещь, которая покрывает' }"/>

	<p> Обратите внимание, что некоторые существительные имеют встроенную связь; в этом случае они образуют связь с последующим существительным/глаголом по-своему:</p>

	<PAT :pairs="{'tool fight' : 'оружие = инструмент, который используют для борьбы', 'document allowed':'лицензия = документ, подтверждающий, что кому-то разрешено что-то' }"/>

	<p> 4. Объект идёт после глагола (потому что мы описываем действие путём указания объекта):</p>

	<PAT :pairs="{'buy object-eat/drink' : 'покупать еду', 'cause-different all':'изменить всё' }"/>

	<p> Обратите внимание, что многие глаголы имеют встроенную связь/предлог: </p>

	<PAT :pairs="{'tense think' : 'быть напряженным из-за размышления', 'commit_crime go-inside':'совершить преступление, заключающееся в проникновении куда-то' }"/>

	<p> Вы можете найти список базовых слов и их грамматичесое описание <router-link to="/elements"> здесь </router-link>. </p>
</div>


<div v-else-if="cur_lesson == 'ke'">

	<div :class="$css.title"> Вопросы с ke </div>

	<p> Слово <nao n="what"/> означает <i>что/который</i>. Давайте посмотрим, как его использовать в предложении:</p>

	<PAT :pairs="{'you eat/drink what?' : 'Что ты ешь? (дословно: ты ешь что)'}"/>

	<p> Можно заметить, что <nao n="what"/> стоит в конце предложения. Это потому что мы должны соблюдать порядок слов SVO. Можно взять на вооружение такое правило: вопросительное слово должно стоять там же, где и ответ.</p>

	<PAT :pairs="{'\'jon gin ke' : 'Что ест Джон?', '\'jon gin pot' : 'Джон ест картошку.'}" />

	<p> Как видите, <nao n="what"/> (вопросительное слово) и <nao n="potato"/> (ответ) стоят в одном месте. Ещё примеры: </p>

	<PAT :pairs="{
		'female-that go that for_the_sake buy what' : 'Она идёт туда, чтобы купить что?',
		'female-that go that for_the_sake buy potato' : 'Она идёт туда, чтобы купить картошку.',
		'what cause-feel-in_danger male-that' : 'Что напугало его?',
		'dencul nenabun muta' : 'Молния напугала его.'
	}" />


	<p> Даже если предложение уже содержит <nao n="what"/>, вы можете дополнительно поставить вопросительную частицу <nao n="QUESTION"/> в начало, это опционально. </p>

	<PAT :pairs="{'(QUESTION) you eat/drink what': 'Что ты ешь/пьёшь?'}" />

	<p> Оно также может быть использовано в сочетании с существительными в значении <span class="english_word"> какой/который </span>. Например: </p>

	<PAT :pairs="{'pot ke' : 'какая картошка', 'dio ke' : 'какая история'}" />

	<p> Все вопросительные слова выводятся из <nao n="what"/>. Самые частые из них: </p>

	<PAT :pairs="{
		'person-what' : '(который человек) = кто',
		'time-what' : '(которое время) = когда',
		'because-what' : '(из-за чего) = почему',
		'be_located-what' : '(в каком местоположении) = где',
		'quantity-what' : '(какого количества) = сколько'
	}" />
</div>



<div v-else-if="cur_lesson == 'tenses'">
	<div :class="$css.title"> Времена глаголов </div>

	<p> Как было упомянуто ранее, по умолчанию глагол не стоит ни в каком времени. Три основных времени: </p>

	<PAT line :pairs="{
		'NOW' : 'настоящее',
		'FUTURE' : 'будущее',
		'PAST' : 'прошедшее',
	}" />

	<PAT line :pairs="{
		'me NOW-go' : 'Я иду',
		'me FUTURE-go' : 'Я пойду',
		'me PAST-go' : 'Я шёл',
	}" />

	<p> Кроме этого есть также аспекты, которые могут помочь вам более точно описать время.</p>

	<p>
		<nao n="inside"/> - длительный аспект, мы делаем акцент на том, что действие происходит в определённый момент или период времени. <br>
		<nao n="outside"/> - мгновенный аспект, мы акцентируем то, что действие проходит за короткое время и заканчивается быстро.
	</p>

	<PAT :pairs="{
		'me inside-see/look female-that' : 'Я смотрю на неё. (длительно, в определённый момент)',
		'me outside-see/look female-that' : 'Я взглянул на неё (одно мгновение)',
	}" />

	<p>
		<nao n="regularly"/> регулярный аспект, действие происходит регулярно<br>
		<nao n="not_regularly"/> нерегулярный аспект, действие произошло один или два раза, нет регулярности
	</p>

	<PAT :pairs="{
		'me PAST regularly-eat/drink potato' : 'Раньше я ел картошку (регулярно в прошлом)',
		'me PAST not_regularly-eat/drink potato' : 'Как-то раз было, что я ел картошку. (нет регулярности)',
	}" />


	<p>
		<nao n="inside-PAST"/> перфект, т.е. действие произошло до определённого момента, и мы видим результат.<br>
		<nao n="inside-FUTURE"/> проспектив, т.е. действие судя по всему произойдёт, исходя из текущего положения вещей
	</p>

	<PAT :pairs="{
		'me inside-PAST eat/drink' : 'Я поел (и результат виден сейчас - я сыт)',
		'inside-FUTURE PASSIVE-do water-sky' : 'Будет дождь (судя по по тучам)',
	}" />

	<p> <nao n="would"/> эквивалентен частице <i>бы</i>: </p>

	<PAT :pairs="{
		'if me have money quantity-big, me would buy building' : 'Если бы у меня было много денег, я бы купил дом',
	}" />

	<p> 
		<b>bui</b> может быть поставлена либо в обе клаузы, либо только в одну из них.
 	</p>

	<p> Мы можем комбинировать вышеизложенные префиксы в таком порядке: (<b>bui</b>) + время + аспект </p>

	<PAT :pairs="{
		'you PAST-inside eat/drink what' : 'Что ты ел? (в тот момент)',
		'from day-next me FUTURE-regularly move-for_the_sake-healthy': 'С завтрашнего дня я буду делать физические упражнения (регулярно)',
		'if you would-PAST cause-know to me, me would-PAST cause-more-find_easy you' : 'Если бы ты мне сказал, я бы тебе помог (но уже поздно)',
		'if me have act_of-work, me not would-NOW-inside-sit be_located object-be_located-NOW' : 'Если бы у меня была работа, я бы тут сейчас не сидел.',
	}" />

	<p> Как и в русском языке, время подчинённого глагола/клаузы относительно главного глагола/клаузы: </p>

	<PAT :pairs="{
		'male-that PAST speak CLAUSE FUTURE cause-more-find_easy' : 'Он сказал, что поможет.',
		'male-that PAST speak CLAUSE PAST notice some' : 'Он сказал, что что-то заметил',
	}" />
</div>

<div v-else-if="cur_lesson == 'clauses'">

	<div :class="$css.title"> Клаузы </div>

	<p> Что такое клауза? Это по сути предложение внутри предложения. В русском языке клауза часто начинается со слова <span class="english_word">что</span>. В Nao любая клауза может быть промаркирована через <nao n="CLAUSE" />: </p>

	<PAT :pairs="{
		'\'petia know CLAUSE \'maca not work day-NOW' : 'Петя знает, что Маша сегодня не работает.',
		'\'maca sad CLAUSE \'petia not eat/drink' : 'Маше грустно, что Петя не ест.',
		'me see/look CLAUSE \'maca work' : 'Я вижу/смотрю, как Маша работает.',
		'QUESTION you want CLAUSE me go' : 'Ты хочешь, чтобы я пошёл?',
	}" />

	<p> Маркер <nao n="CLAUSE" /> опционален, когда он очевиден из контекста. Поэтому его можно убрать из вышеизложенных предложений. </p>

	<p> Также клауза может быть вопросительной, тогда для её маркировки используется частица <nao n="QUESTION" />, эквивалетная частице <span class="english_word">ли</span> в русском языке: </p>

	<PAT :pairs="{
		'\'petia know QUESTION \'maca work day-NOW' : 'Петя знает, работает ли Маша сегодня.',
	}" />

	<p> Если вопросительная клауза содержит <nao n="what" />, тогда частица <nao n="QUESTION" /> опциональна. </p>

	<PAT :pairs="{
		'me not understand (QUESTION) you want what' : 'Я не понимаю, чего ты хочешь.',
		'female-that LITTLE-want-know (QUESTION) because-what person-all stop-speak' : 'Ей интересно, почему все замолчали.'
	}" />

	<p> Клаузы могут также присоединяться к существительным: </p>

	<PAT :pairs="{
		'mu pe (ke) pulua' : 'Мужчина, который кричал',
		'da pe me kan (ke)' : 'Человек, которого я видел',
		'dia pe (leke) mit te' : 'День, когда я встретил тебя',
		'female-adult CLAUSE me work with male-sibling person-what': 'Женщина, с братом которой я работаю',
	}" />

	<p> Как видно из примеров выше, <nao n="what" /> играет роль слова <i>который</i>, но в отличии от русского его можно опустить (тогда говорят о клаузе с неявной связью). При этом <nao n="CLAUSE" /> в начале такой клаузы является обязательной. </p>
</div>




<div v-else-if="cur_lesson == 'location-and-direction'">
	<div :class="$css.title"> pa </div>

	<p> Изначально это глагол, означающий <span class="english_word">находиться (где-то)</span>, поэтому мы можем сказать: </p>

	<PAT :pairs="{'me be_located \'boston' : 'Я в Бостоне.'}"/>

	<p> Но он также используется как наречие: </p>

	<PAT :pairs="{'me work be_located \'boston' : 'Я работаю в Бостоне.'}"/>

	<p> Получается, что <nao n="be_located"/> как правило будет переводится на русский язык как <span class="english_word">в/на</span>. </p>

	<p> Чтобы говорить о местоположении с большей точностью, у нас есть следующие слова: </p>

	<PAT :little_columns="[
		{'inside': 'внутри', 'outside': 'снаружи' },
		{'front': 'перед', 'backside': 'зад' },
		{'right': 'право', 'left': 'лево' }
	]" />

	<p> Все слова выше являются существительными, поэтому они часто используются в паре с <nao n="be_located"/>. </p>

	<PAT :pairs="{
		'stick_shaped-write be_located \'mesa' : 'Ручка находится … столе (предлог угадывается из контекста).',
		'stick_shaped-write be_located top \'mesa' : 'Ручка на столе.',
		'stick_shaped-write be_located bottom \'mesa' : 'Ручка под столом.',
		'stick_shaped-write be_located inside \'mesa' : 'Ручка в столе.',
	}" />

	<div :class="$css.title"> tu (к), ti (от) </div>

	<p> Эта пара используется, чтобы говорить о направлениях. Например: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'go-fast to place-buy' : 'Бежать к магазину.',
		'go-fast to inside place-buy' : 'Вбежать внутрь магазина.',
		'go-fast to outside place-buy' : 'Выбежать из магазина.',
		'go-fast to backside place-buy' : 'Забежать за магазин.',
		'GIANT-do_voice from building' : 'Кричать из магазина.',
		'GIANT-do_voice from backside building' : 'Кричать из-за магазина (находясь за ним)',
	}" />

</div>

<div v-else-if="cur_lesson == 'prefixes'">
	<div :class="$css.title"> Префиксы </div>

	<p> Префиксы это маленькие слова, которые ставятся вначало слова и модифицируют его.</p>

	<p class="text-h5 text-center"> <nao n="GIANT"/> </p>

	<p> Увеличительный префикс, имеется переход от количества к качеству:  </p>

	<PAT :little_columns="[
		{'good': 'хороший', 'GIANT-good': 'замечательный'},
		{'convinced': 'считать/полагать', 'GIANT-convinced': 'быть уверенным'},
		{'PASSIVE-find_funny': 'смешной', 'GIANT-PASSIVE-find_funny': 'уморительный'},
		{'angry': 'злиться', 'GIANT-angry': 'быть в ярости'},
	]" />

	<p class="text-h5 text-center"> <nao n="LITTLE"/> </p>

	<p> Уменьшительный префикс, имеется переход от количества к качеству: </p>

	<PAT :little_columns="[
		{'female': 'особь женского пола', 'LITTLE-female': 'девочка'},
		{'cut': 'резать', 'LITTLE-cut': 'порезаться, поцарапать'},
		{'eat/drink': 'есть', 'LITTLE-eat/drink': 'перекусить'},
		{'convinced': 'считать/полагать', 'LITTLE-convinced': 'предполагать'},
	]" />

	<p class="text-h5 text-center"> <nao n="o_great"/> </p>

	<p> Этот суффикс может придать любому слову оттенок поэтичности, важности, высокого стиля:</p>

	<PAT :little_columns="[
		{'good': 'хороший', 'o_great-good': 'чудесный'},
		{'begin-want': 'захотеть', 'o_great-begin-want': 'возжелать'},
		{'stop-sleep': 'проснуться', 'o_great-stop-sleep': 'пробудиться'},
	]" />

	<p class="text-h5 text-center"> <nao n="ANGRY"/> </p>

	<p> Матерный префикс, позволяющий превратить любое слово в ругательство: </p>

	<PAT :little_columns="[
		{'bad': 'плохой', 'ANGRY-bad': 'х**вый'},
		{'person-prone_not-understand': 'дурак', 'ANGRY-person-prone_not-understand': 'долб**б'},
		{'not-feel-important' : 'безразлично', 'ANGRY-not-feel-important' : 'пох**'},
	]" />

	<p class="text-h5 text-center"> <nao n="INFORMAL"/> </p>

	<p> Слэнговый префикс, превращающий любое слово в неформальное, для использования в компании друзей: </p>

	<PAT :little_columns="[
		{'good': 'хороший', 'INFORMAL-good': 'зачётный'},
		{'male': 'особь мужского пола', 'INFORMAL-male': 'чувак'},
		{'vehicle_with_cabin-solid': 'автомобиль', 'INFORMAL-vehicle_with_cabin-solid': 'тачка'},
	]" />
</div>

<div v-else-if="cur_lesson == 'prefix-like-words'">
	<div :class="$css.title"> Префиксоподобные слова </div>

	<p> Есть группа слов, которые грамматически являются префиксами, но обычно пишутся отдельно и воспринимаются многими как наречия. К примеру, слово <nao o="sol"/> = только:</p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'only me eat/drink potato' : 'Только я ем картошку.',
		'me only eat/drink potato' : 'Я только/просто ем картошку (больше ничего не делаю)',
		'me eat/drink only potato' : 'Я ем только картошку.',
	}" />

	<p> Как видите, положение слова <nao n="only"/> меняет значение предложения. </p>

	<p> Теперь давайте рассмотрим слово <b>nui</b> = тоже/также: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'also male-that go \'tokio' : 'Он тоже поедет в Токио.',
		'male-that also go \'tokio' : 'Также он поедет в Токио.',
		'male-that go also \'tokio' : 'Он поедет и в Токио тоже.',
	}" />

	<p> Слово <b>bis</b> используется, чтобы сделать ударение на конкретном слове в предложении: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'EMPHASIS \'petia in_love \'maca' : 'это ПЕТЯ любит Машу (а не Коля)',
		'\'petia EMPHASIS in_love \'maca' : 'Петя ЛЮБИТ Машу (а не ненавидит).',
		'\'petia in_love EMPHASIS \'maca' : 'Петя любит МАШУ (а не Свету)',
	}" />

	<p> Наконец, слово, которое было заимствовано из русского языка и работает точно так же - <b>dac</b> = даже: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'even \'jenia see/look cat' : 'Даже Женя видел кота.',
		'\'jenia even see/look cat' : 'Женя даже видел кота.',
		'\'jenia see/look even cat' : 'Женя видел даже кота.',
	}" />

</div>

<div v-else-if="cur_lesson == 'senses'">
	<div :class="$css.title"> 5 чувств </div>

	<p> Вначале давайте взглянем на слова, описывающие пять основных чувств:</p>

	<PAT line :pairs="{
		'see/look' : 'видеть/смотреть',
		'hear/listen' : 'слышать/слушать',
		'smell' : 'чуять/нюхать',
		'taste' : 'чувствовать вкус/смаковать',
		'in_contact-physical' : 'прикасаться'
	}" />

	<p> Но можно ли тогда отличить <i>смотреть</i> от <i>видеть</i>? Да, в зависимости от того, какое именно отличие вам нужно. Во-первых, у нас есть слово <nao n="notice"/> = замечать. Мы можем использовать его в сочетании с вышеуказанными словами:</p>

	<PAT line :pairs="{
		'see/look-and_as_a_result-notice' : 'видеть',
		'hear/listen-and_as_a_result-notice' : 'слышать',
		'smell-and_as_a_result-notice' : 'чуять',
		'taste-and_as_a_result-notice' : 'чувствовать вкус',
		'in_contact-physical-and_as_a_result-notice' : 'чувствовать прикосновение'
	}" />

	<p> Все эти слова подразумевают, что мозг регистрирует наличие чего-то, это происходит само по себе, неосознанно.</p>

	<p> С другой стороны, у нас есть слово <nao n="concentrated"/> = быть сосредоточенным: </p>

	<PAT line :pairs="{
		'concentrated-see/look' : 'смотреть',
		'concentrated-hear/listen' : 'слушать',
		'concentrated-smell' : 'нюхать',
		'concentrated-taste' : 'смаковать',
		'concentrated-in_contact-physical' : 'трогать'
	}" />

	<p> Эти слова подразумевают, что действие осознанное, внимательное, человек сосредоточен. </p>

</div>

<div v-else-if="cur_lesson == 'transliteration'">
	<div :class="$css.title"> Транслитерация </div>
	<p> При записи иностранных слов (например имён собственных), необходимо адаптировать написание этих слов под орфографию Nao согласно <router-link to="/transliterations"> официальным правилам транслитерации</router-link>. </p>

	<p> Зачем это нужно? Представьте, что кто-то посылает вам сообщение на Nao:</p>

	<div class="pl-5 pb-5"> <v-icon color="red"> {{ $mdi.Close }} </v-icon> joimit – me nom Krzysztof Wojciechowski </div>

	<p> Если вы не знаете оригинального языка (в этом случае - польский), вас смутит это имя, вы не будете иметь ни малейшего понятия, как это произносится. Это нарушило бы одно из основных правил Nao: все слова читаются, как пишутся. Поэтому абсолютно недопустимо писать иностранные слова в оригинальной орфографии. Вместо этого, этот поляк должен был адаптировать своё имя согласно <router-link to="/transliterations/Polish"> официальных правил транслитерации для польского языка</router-link> и переписать своё сообщение следующим образом: </p>

	<div class="pl-5 pb-5"> <v-icon color="green"> {{ $mdi.Check }}  </v-icon> joimit – me nom 'kcyctof 'voitcehovski </div>

	<p> Теперь вы точно знаете, как произнести это имя - буква за буквой, как и любое другое слово в Nao.</p>

	<p> Это касается в том числе и русского языка, поэтому настоятельно рекомендуется ознакомиться с <router-link to="/transliterations/Polish"> транслитерацией для русского</router-link>. Таким образом, чтобы узнать, как некое иностранное слово пишется на Nao, вы должны обращаться к официальным правилам. </p>

	<p> Также обратите внимание, что транслитерация не ставит задачи идеально передать оригинальное произношение. Транслитерация - это компромис между усреднённым произношением, фонемами и написанием. Благодаря официальным правилам транслитерации, каждое иностранное слово имеет только один правильный способ написания и произношения в Nao. </p>
</div>

<div v-else-if="cur_lesson == 'numbers'">
	<div :class="$css.title"> Числа </div>

	<p> Имеется 10 цифр, которые необходимо запомнить:</p>

	<PAT :little_columns="[
		{'0': 'nol', '1': 'ji', '2': 'dua', '3':'tui', '4':'fos'},
		{'5': 'pet', '6': 'lok', '7': 'set', '8':'hap', '9':'nef'},
	]" />

	<p> Далее, чтобы сказать любое число, можно просто перечислить его цифры (и <nao o="dot"/> = точка): </p>

	<PAT :little_columns="[
		{'10': 'jinol', '398': 'tuinefhap', '2017': 'duanoljiset', '3.14':'tuidotjifos'},
	]" />

	<p> Для выражения больших чисел используется <b>pu</b> + количество нулей: </p>

	<PAT :little_columns="[{
		'GIANT-three' : '1,000',
		'eight GIANT-three' : '8,000',
		'GIANT-six' : '1,000,000',
		'one-two GIANT-six' : '12,000,000',
	}]" />

	<p> Для выражения маленьких чисел используется <b>pi</b> + количество нулей: </p>

	<PAT :little_columns="[{
		'LITTLE-three' : '0.001',
		'four LITTLE-three' : '0.004',
		'LITTLE-six' : '0.000001',
		'nine-four LITTLE-six' : '0.000094',
	}]" />

	<p> Чтобы указать на количество объектов, число ставится перед существительным: </p>

	<PAT :pairs="{
		'five dog' : 'пять собак',
		'eight GIANT-three money-USA' : 'восемь тысяч долларов США',
	}" />

	<p> Порядковые числительные образуются при помощи <b>hai</b>: </p>

	<PAT :pairs="{
		'organized-one' : 'первый',
		'organized-four-two' : 'сорок-второй',
	}" />

	<p> Числа очень полезны в Nao. Они используются для образования различных слов, описывающих время. Например названия дней недели:</p>

	<PAT line :pairs="{
		'weekday' : 'неделя',
		'weekday-one' : 'понедельник',
		'weekday-two' : 'вторник',
		'weekday-seven' : 'воскресенье',
	}" />

	<p> Также названия месяцев: </p>

	<PAT line :pairs="{
		'element-year' : 'месяц',
		'element-year-one' : 'январь',
		'element-year-two' : 'февраль',
		'element-year-one-two' : 'декабрь',
	}" />

	<p> Пример даты: </p>

	<PAT no_pronounce :pairs="{
		'hour-19 minute-45 day-24 element-year-12 year-2020' : '19:45, 24 декабря 2020',
	}" />

	<p> Как видите, числа можно записывать словами или цифрами - это эквивалентно. Обратите внимание, что в дате число идёт после существительного, это важно. </p>
</div>


<div v-else-if="cur_lesson == 'family-members'">
	<div :class="$css.title"> Члены семьи </div>

	<p> Мощное словообразование в Nao позволяет образовать слова для описания всех кровных родственников, используя лишь три базовых слова: </p>

	<PAT line :pairs="{
		'parent' : 'родитель',
		'male' : 'особь мужского пола',
		'sibling' : 'брат/сестра',
	}" />

	<p> Из двух из них мы образуем антонимы: </p>

	<PAT line :pairs="{
		'offspring' : 'ребёнок',
		'female' : 'особь женского пола',
	}" />

	<p> Начнём с простого: </p>

	<PAT :pairs="{
		'female-parent' : 'мать',
		'male-parent' : 'отец',
		'female-offspring' : 'дочь',
		'male-offspring' : 'сын',
		'female-sibling' : 'сестра',
		'male-sibling' : 'брат',
	}" />

	<p> Далее, если сказать <nao n="parent-parent"/>, это значит <i>родитель родителя</i>: </p>

	<PAT :pairs="{
		'female-parent-parent' : 'бабушка',
		'male-parent-parent' : 'дедушка',
		'female-offspring-offspring' : 'внучка',
		'male-offspring-offspring' : 'внук',
	}" />

	<p> Можно также повысить точность, указывая пол на каждом <b>pau/pai</b>: </p>

	<PAT :pairs="{
		'female-parent-female-parent' : '(мама мамы) = бабушка по маминой линии',
		'male-offspring-female-offspring' : 'сын дочери',
	}" />

	<p> Повторив <nao n="parent"/> трижды и более раз, мы получим слово <i>прадедушка/прабабушка</i> и так далее. Во избежание повторения, можно воспользоваться числами:</p>

	<PAT :pairs="{
		'female-parent-three' : 'прабабушка',
		'female-parent-five' : 'прапрапрабабушка',
	}" />

	<p> Числа также используются для указания родства братьев: </p>

	<PAT :pairs="{
		'male-sibling' : 'родной брат',
		'male-sibling-two' : 'двоюродный брат',
		'male-sibling-three' : 'троюродный брат',
	}" />

	<p> Идём дальше: </p>

	<PAT :pairs="{
		'female-sibling-parent' : '(сестра родителя) = тётя ',
		'male-sibling-parent' : '(брат родителя) = дядя',
		'female-offspring-sibling' : '(дочь брата/сестры) племянница',
		'male-offspring-sibling' : '(сын брата/сестры) племянник',
		'female-sibling-parent-parent' : 'сестра бабушки/дедушки',
		'male-sibling-parent-parent' : 'брат бабушки/дедушки',
		'female-offspring-offspring-sibling' : '(внучка сестры/брата) = внучатая племянница',
		'male-offspring-offspring-sibling' : '(внук сестры/брата) = внучатый племянник',
	}" />

	<p> Далее берём на вооружение слово <nao show_tr n="married"/>: </p>

	<PAT :pairs="{
		'person-married' : 'супруг/супруга',
		'female-married' : 'жена',
		'male-married' : 'муж',
	}" />

	<p> Используя эти слова, мы можем легко составить любое слово для родственников по браку: </p>

	<PAT :pairs="{
		'female-parent-female-married' : '(мать жены) = тёща',
		'female-married-male-offspring' : '(жена сына) = сноха',
		'male-sibling-female-married' : '(брат жены) = шурин',
	}" />

	<p> Наконец, имеется слово <b>tia</b>: </p>

	<PAT :pairs="{
		'female-parent-side' : 'мачеха',
		'male-parent-side' : 'отчим',
		'female-offspring-side' : 'падчерица',
		'male-offspring-side' : 'пасынок',
		'female-sibling-side' : 'сводная сестра',
		'male-sibling-side' : 'сводный брат',
	}" />

</div>

<div v-else-if="cur_lesson == 'colors'">
	<div :class="$css.title"> Цвета </div>
	<p> Система цветов в Nao очень мощна - она позволяет выразить абсолютно любой цвет и оттенок. Она основана на модели HSL (тон, насыщенность и светлота). Если посмотреть на цветовой круг внизу, можно заметить 6 базовых цветов (те же, что 7 цветов радуги, но оранжевый является промежуточным). Кликните на любой цвет в круге, чтобы лучше понять систему.</p>

	<div class="d-flex justify-center pb-5">
		<Colorwheel :size="cw_size" />
	</div>

	<p> Как же она работает? Сначала необходимо запомнить 6 базовых цветов: </p>

	<PAT line no_pronounce :pairs="{
		'color-0 (color-zero)' : 'красный',
		'color-1 (color-one)'  : 'жёлтый',
		'color-2 (color-two)' : 'зелёный',
		'color-3 (color-three)' : 'голубой',
		'color-4 (color-four)' : 'синий',
		'color-5 (color-five)' : 'маджента',
	}" />

	<p> Для краткости можно опускать конечные нули: </p>
	
	<p class="pl-8"> <nao n="color-10 = color-1" />  </p>

	<p> Далее мы можем образовывать промежуточные цвета. Например, между <nao n="color-0-0"/> и <nao n="color-1-0"/>: </p>

	<PAT :pairs="{
		'color-05' : 'оранжевый',
		'color-08' : 'золотой',
	}" />

	<p> Далее можно применить слова <nao n="light"/> (светлый) и <nao n="dark"/> (тёмный) чтобы образовать больше оттенков: </p>

	<PAT :pairs="{
		'color-05 dark' : '(тёмно-оранжевый) = коричневый',
		'color-5 light' : '(светлая маджента) = розовый',
	}" />

	<p> Также используется слово <nao n="weak"/> (слабый), чтобы понизить насыщенность цвета (проще говоря, смешать его с серым): </p>

	<PAT :pairs="{
		'color-zero-weak' : 'серовато-красный',
	}" />

	<p>Есть три особых цвета:</p>

	<PAT line :pairs="{
		'color-completely-light' : '(полностью светлый цвет) = белый',
		'color-completely-dark' : '(полностью тёмный цвет) = чёрный',
		'color-completely-weak' : '(полностью слабый цвет) = серый',
	}" />

	<p> Вы также можете вбить в словарь любой цвет, который составите сами, и вам покажет этот цвет. <router-link to="/dict/hanolpetlau_hagalai_ha35_lai7"> Попробуйте! </router-link> </p>
</div>

<div v-else-if="cur_lesson == 'verbal-emoji'">
	<div :class="$css.title"> Словесные эмодзи </div>

	<p> Как вы уже знаете, Nao не использует восклицательных, вопросительных знаков, троеточия и прочее. К счастью, у Nao есть гораздо более мощный и однозначный инструмент - словесные эмодзи: </p>

	<PAT no_pronounce :pairs="{
		'te fa ke =hal=' : 'Что ты делаешь? )))',
	}" />

	<p> Как видите, <b>=hal=</b> это словесный эмодзи и он эквивалентен обычным эмодзи или, например, английскому <i>LOL</i>. Чтобы образовать словесный эмодзи, необходимо просто выбрать слово, которое описывает ваше отношение к фразе либо ваши эмоции и поставить его в начало либо в конец предложения (вместо тире, если таковой был). </p>

	<PAT no_pronounce :pairs="{
		'me no ku go ta =nabun=' : 'Я туда не пойду (страшно)',
		'moikofomodei te sel =joi=' : 'Надеюсь, у тебя получится (рад)',
		'danel gobepa =pon=' : 'Кто-то приехал (важно)',
		'hoike fa ta =punajus=' : 'Зачем ты это сделал?! (очень удивлён)',
	}" />
</div>

<div v-else-if="cur_lesson == 'passive'">
	<div :class="$css.title"> Пассив </div>

	<p> Пассив это специальная форма глагола, которая ставит объект действия на первое место. Чтобы образовать пассив, необходимо поставить <nao o="do"/> в начало глагола. </p>

	<PAT :pairs="{
		'cat PAST eat/drink mouse' : 'Кошка съела мышку.',
		'mouse PAST PASSIVE-eat/drink by cat' : 'Мышка была съедена кошкой.',
	}" />

	<p> Как видите, указать агента действия можно через специальный предлог <nao o="doc"/>. </p>

	<p> Пассив в Nao играет огромную роль и используется очень часто. Это важный инструмент, позволяющий вынести объект на первое место. Давайте рассмотрим такое предложение: </p>

	<PAT :pairs="{
		'male-that like see/look act_of-on_fire' : 'Он любит смотреть на огонь.',
	}" />

	<p> Теперь давайте перевернём это предложение, вынеся <i>огонь</i> в начало: </p>

	<PAT :pairs="{
		'act_of-on_fire PASSIVE like see/look by male-that' : 'Огонь приятен глазу для него',
	}" />

	<p> Здесь мы говорим о конкретном человеке и его предпочтениях. Но что если многие люди любят смотреть на огонь? Тогда это можно рассматривать как свойство огня, а именно его красоту:</p>

	<PAT :pairs="{
		'act_of-on_fire PASSIVE like see/look' : 'Огонь красив.',
	}" />

	<p> Удалив пробелы, мы получаем слово <b>dofikan</b> = красивый. </p>

	<p> Вот ещё пример, вместе с <nao n="cause"/>: </p>

	<PAT :pairs="{
		'female-that feel-in_danger dog' : 'Она боится собак.',
		'dog PASSIVE-feel-in_danger' : 'Собаки страшные.',
		'dog cause-feel-in_danger male-that' : 'Собака напугала его.',
	}" />

	<p> Вот ещё подобные группы слов: </p>

	<PAT :little_columns="[
		{'bored': 'испытывать скуку', 'PASSIVE-bored': 'скучный', 'cause-bored' : 'наскучить' },
		{'feel-extraordinary': 'быть удивлённым', 'PASSIVE-feel-extraordinary': 'удивительный', 'cause-feel-extraordinary' : 'удивить' },
		{'find_funny': 'считать смешным', 'PASSIVE-find_funny': 'смешной', 'cause-find_funny' : 'смешить' },
	]" />

</div>


<div v-else-if="cur_lesson == 'common-confusions'">
	<div :class="$css.title"> Частые ошибки </div>

	<p class="text-h5 text-center"> <nao n="see/look"/> vs <nao n="notice"/> </p>

	<p> Оба слова могут переводиться как <i>видеть</i>, в чём же разница? </p>

	<p>
		<nao n="see/look"/> означает получение визуальной информации через глаза <br>
		<nao n="notice"/> означает заметить что-то, не обязательно глазами. Это значит получение информации в мозг о наличии чего-то или какого-то факта.
	</p> 

	<PAT :pairs="{
		'me PAST notice person-that eat/drink' : 'Я видел (или слышал, или унюхал), как он ест (был шанс, что я бы не заметил)',
		'me PAST see/look person-that eat/drink' : 'Я видел (или смотрел), как он ест',
	}" />

	<p> Более подробно об этом можно почитать в уроке о <router-link to="/textbook/senses">5 чувствах</router-link>. </p>

	<p class="text-h5 text-center"> <nao n="think"/> vs <nao n="convinced"/> </p>

	<p>
		<nao n="convinced"/> подразумевает иметь мнение, убеждение <br>
		<nao n="think"/> это мыслительное процесс, например когда ты пытаешься решить задачу или воображаешь что-то
	</p>

	<PAT :pairs="{
		'me convinced female-that commit_crime' : 'Я считаю, что она совершила преступление',
		'me think female-that commit_crime' : 'Я воображаю, как она совершает преступление',
		'you convinced what' : 'Как ты думаешь? (твоё мнение?)',
		'you think what' : 'О чём ты думаешь?',
	}" />

	<p class="text-h5 text-center"> <nao n="for_the_sake"/> </p>

	<p> Некоторые люди ошибочно полагают, что <nao n="for_the_sake"/> идентичен предлогу <i>для</i>. В реальности же у предлога <i>для</i> много значений, в то время как  <nao n="for_the_sake"/> = <i>ради</i> или <i>чтобы</i>. </p>

	<PAT :pairs="{
		'me PAST-do that for_the_sake you' : 'Я сделал это ради тебя',
		'male-that begin-know-with-understand language-Polish for_the_sake go country-Polish' : 'Он изучает польский, чтобы поехать в Польшу.',
	}" />

	<p class="text-h5 text-center"> <nao n="place"/> vs <nao n="object-be_located"/> </p>

	<p> <nao n="object-be_located"/> значит <i>место</i> в общем смысле, это простое слово. Однако, <nao n="place"/> - слово гораздо более конкретное, обозначающее место с конкретной функцией или свойством, в которое можно прийти и что-то там делать. Поэтому не используйте <b>ma</b>, если вы не понимаете это слово. </p>

</div>

<div v-else-if="cur_lesson == 'associativity'">
	<div :class="$css.title"> Ассоциативность </div>

	<p> Ассоциативность это одна из главных особенностей Nao. Главным образом она подразумевает неважность пробелов, неявные границы между словами. Давайте посмотрим на несколько примеров:</p>

	<p> <b>henodutua</b> можно разобрать следующими способами: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'ask-not stop-try': 'не сдавайся',
		'ask not-stop try': 'прошу продолжать пытаться'
	}" />

	<p> Переводы получаются разные по форме, но одинаковые по смыслу, подобно как в математике одно и то же число иногда может быть записано разными способами. Ещё примеры: </p>

	<PAT line :pairs="{
		'go-outside building': 'покинуть здание',
		'go outside-building': 'пойти на улицу'
	}" />

	<PAT line :pairs="{
		'male-parent-parent me': 'мой дедушка',
		'male-parent parent-me': 'отец моего родителя',
	}" />

	<PAT line :pairs="{
		'person-cause-healthy-bodypart-bite you': 'мой стоматолог',
		'person-cause-healthy bodypart-bite you': 'врач моих зубов',
	}" />

	<PAT line :pairs="{
		'eight GIANT-three-meter': '8 километров',
		'eight-GIANT-three meter': '8000 метров',
	}" />

	<p> Все эти слова могли бы быть записаны вообще без пробелов, смысл бы не изменился. </p>

	<p> Именно ассоциативность позволяет нам менять смысл слова на противоположный, даже если мы не знакомы с его структурой:</p>

	<PAT line :pairs="{
		'person-prone-convinced-FUTURE-good': 'оптимист',
		'person-prone-convinced-FUTURE-bad': 'пессимист',
	}" />
</div>


<div v-else-if="cur_lesson == 'constructing-verbs' ">
	<div :class="$css.title"> Строим новые глаголы </div>

	<p> В этом уроке мы выучим маленькие, но мощные элементы, которые помогают конструировать новые глаголы. Во многих других языках для этой цели используются различные суффиксы. Однако в Nao понятия "суффикс" нет, всё нижеперечисленное - это самостоятельные глаголы.</p>

	<p class="text-h5 text-center"> <nao n="begin"/> = начать </p>

	<p> Большинство глаголов в Nao это состояния/процессы. Глагол <nao n="begin"/> используется, чтобы обозначить начало/переход в данное состояние/процесс.</p>

	<PAT :pairs="{
		'begin-want': '(начать хотеть) = захотеть',
		'begin-cold': '(начать быть холодным) = похолодать',
		'begin-dead': '(начать быть мёртвым) = умереть',
		'begin-know': '(начать знать) = узнать',
		'begin-different': '(начать быть другим) = измениться'
	}" />

	<p class="text-h5 text-center"> <nao n="stop"/> = перестать </p>

	<p> Это противоположность вышеуказанного глагола. Используется аналогично. Часто переводится как <i>больше не </i>: </p>

	<PAT :pairs="{
		'stop-want': '(перестать хотеть) = расхотеть',
		'stop-remember': '(перестать помнить) = забыть',
		'stop-on_fire': '(перестать гореть) = потухнуть',
	}" />

	<p class="text-h5 text-center"> <nao n="cause"/> = сделать так, чтобы... </p>

	<p> Этот маленький глагол - это самый частый элемент в словаре. Его польза не может быть переоценена, так как он позволяет составить бесчисленное количество новых понятий:</p>

	<PAT :pairs="{
		'cause-dead': '(сделать так, чтобы был мёртв) = убить',
		'cause-on_fire': '(сделать так, чтобы горел) = поджечь',
		'cause-know': '(сделать так, чтобы знал) = рассказать/проинформировать',
		'cause-safe': '(сделать так, чтобы был в безопасности) = защитить',
	}" />

	<p> Таким образом, Nao, как и русский язык, различает переходные и непереходные глаголы: </p>

	<PAT :pairs="{
		'begin-different': 'измениться',
		'cause-different': 'изменить',
		'begin-open' : 'открыться',
		'cause-open' : 'открыть',
		'stop-go' : 'остановиться',
		'cause-stop-go' : 'остановить',
	}" />

	<p class="text-h5 text-center"> <nao n="prone"/> = быть склонным (к) </p>

	<p> Используется, чтобы указать на черты характера или закономерности в поведении. </p>

	<PAT :pairs="{
		'prone-feel-in_danger': '(склонный бояться) = трусливый',
		'prone-know': '(склонный знать) = умный',
		'prone-speak-quantity-big': '(склонный много говорить) = разговорчивый',
		'prone-find_difficult-choose' : '(склонный испытывать трудности в выборе) = нерешительный',
	}" />

	<p class="text-h5 text-center"> <nao n="undo"/> = отменить </p>

	<p> Очень полезное слово, которое работает как Ctrl+Z для любого действия. </p>

	<PAT :pairs="{
		'undo-write': '(отменить + написать) = стереть',
		'undo-eat/drink': '(отменить + есть/пить) = стошнить',
		'undo-cause-injured': '(отменить + сломать) = починить',
	}" />
</div>

<div v-else-if="cur_lesson == 'constructing-nouns' ">
	<div :class="$css.title"> Составляем новые существительные </div>

	<p> Частый вопрос среди начинающих - как делать существительные в Nao? Это зависит от того, какой именно смысл вам нужен. В этом уроке мы познакомимся с самыми частыми элементами, решающими эту задачу.</p>

	<p class="text-h5 text-center"> <nao n="person"/> = человек </p>

	<p> Очень простое существительное, которое обозначает человека, совершающего действие или обладающего каким-то свойством. Часто соответствует суффиксам <i>-тель</i>, <i>-ник</i> в русском языке: </p>

	<PAT :pairs="{
		'person-work': '(человек, который работает) = работник',
		'person-cause-healthy': '(человек, который лечит) = врач',
		'person-use': '(человек, который пользуется) = пользователь',
		'person-see/look': '(человек, который видит/смотрит) = зритель',
	}" />

	<p class="text-h5 text-center"> <nao n="tool"/> = инструмент </p>

	<PAT :pairs="{
		'tool-fight': '(инструмент + сражаться) = оружие',
		'tool-light': '(инструмент + светить) = лампа/фонарь',
		'tool-time': '(инструмент + время) = часы',
		'tool-see/look-space': '(инструмент + смотреть + космос) = телескоп',
	}" />

	<p class="text-h5 text-center"> <nao n="act_of"/> = факт/процесс </p>

	<p> Это слово на самом деле никак не меняет значение глагола. Оно больше похоже на маркер с чисто грамматической функцией образования отглагольного существительного. </p>

	<PAT :pairs="{
		'act_of-begin-dead': '(факт/процесс + умереть) = смерть',
		'act_of-explode': '(факт/процесс + взорваться) = взрыв',
		'act_of-warm/hot': '(факт/процесс + тёплый/горячий) = тепло/жара',
		'act_of-fight': '(факт/процесс + сражаться) = сражение',
		'act_of-live': '(факт/процесс + жить) = жизнь',
		'act_of-cook': '(факт/процесс + готовить еду) = готовка',
	}" />

	<p class="text-h5 text-center"> <nao n="someone/something"/> = вещь (которая...) </p>

	<p> Используется, чтобы сослаться на субъект глагола: </p>

	<PAT :pairs="{
		'someone/something-cover': '(вещь, которая покрывает) = покрытие',
		'someone/something-live': '(вещь, которая живёт) = живое существо',
		'someone/something-small': '(вещь, которая маленькая) = маленькая вещь',
	}" />

	<p class="text-h5 text-center"> <nao n="object"/> = объект </p>

	<p> Одно из самых частых элементов в языке, которое используется, чтобы сослаться на объект глагола. По сути оно является более коротким способом сказать <nao n="someone/something-PASSIVE"/>: </p>

	<PAT :pairs="{
		'object-know': '(то, что знают) = знание',
		'object-sell': '(то, что продают) = товар',
		'object-more-like': '(то, что предпочитают) = предпочтения',
