		'object-have': '(то, чем обладают) = собственность',
	}" />

	<p class="text-h5 text-center"> <nao n="place"/> = место (для) </p>

	<p> Это слово обозначает место, куда человек может прийти, оставаться там. Это всегда место (обычно общественное) с определённым свойством или функцией.</p>

	<PAT :pairs="{
		'place-buy': '(место для + покупать) = магазин',
		'place-cause-healthy': '(место для + лечить) = больница',
		'place-eat/drink-alcohol': '(место для + пить алкоголь) = бар',
