</div>

</div></template>

<script>

import PAT from '@/components/PhrasesAndTranslations'
import nao from '@/components/Naoword'
import Awtable from '@/components/Awtable'
import Colorwheel from '@/components/Colorwheel'

export default {
	components: {PAT, nao, Awtable, Colorwheel},
	data() {
		this.all_lessons = [
			{route: 'alphabet-and-pronunciation', name: 'Алфавит и произношение'},
			{route: 'phrases', name: 'Фразы' },
			{route: 'simple-sentences', name: 'Строим простые предложения' },
			{route: 'opposite', name: 'Образование антонимов' },
			{route: 'ke', name: 'Вопросы с ke' },
			{route: 'word-structure', name: 'Структура слова'},
			{route: 'main-grammar-rule', name: 'Главное правило грамматики'},
			{route: 'constructing-nouns', name: 'Составляем существительные'},
			{route: 'constructing-verbs', name: 'Составляем глаголы'},
			{route: 'associativity', name: 'Ассоциативность'},
			{route: 'clauses', name: 'Клаузы' },
			{route: 'tenses', name: 'Времена глаголов' },
			{route: 'location-and-direction',  name: 'Местоположение и направление' },
			{route: 'prefixes',  name: 'Префиксы' },
			{route: 'prefix-like-words',  name: 'Префиксоподобные слова' },
			{route: 'senses', name: 'Ощущения'},
			{route: 'transliteration', name: 'Транслитерация'},
			{route: 'numbers', name: 'Числа'},
			{route: 'family-members', name: 'Члены семьи'},
			{route: 'colors', name: 'Цвета'},
			{route: 'verbal-emoji', name: 'Словесные эмодзи'},
			{route: 'passive', name: 'Пассивная форма'},
			{route: 'common-confusions', name: 'Частые ошибки'},
		];

		this.all_lessons_names = this.all_lessons.map(x => x.name);
		this.all_lessons_routes = this.all_lessons.map(x => x.route);

		return {
			i: this.all_lessons_routes.indexOf(this.$route.params.lesson),
		}
	},
	watch: {
		i(nv, ov) {
			this.$router.push('/textbook/' + this.all_lessons_routes[this.i]).catch(function(err){});
			//window.scrollTo(0, 0);
		},
		$route (to, from){
	        this.i = this.all_lessons_routes.indexOf(this.$route.params.lesson);
	    }
	},
	methods: {

	},
	created() {
		if (this.i == -1) this.i = 0;
		//this.i = this.all_lessons_routes.indexOf(this.$route.params.lesson) || 0;
	},
	computed: {
		cw_size() {
			return {xs:0.4, sm:0.6, md:0.65, lg:0.8, xl:0.9}[this.$vuetify.breakpoint.name];
		},
		cur_lesson() {
			return this.all_lessons_routes[this.i];
		},
		phrases() {
			let res = {};

`joimit				Привет!
me nom 'petia		Меня зовут Петя.
te nom ke			Как тебя зовут?
te najan ke			Как настроение?
li					Хорошо.
lu					Плохо.
joikohunfali		Спасибо.
nalufalu			Извините.
no tal				Не за что / Ничего страшного
dei					Да
deu					Нет
me hom				Я понимаю.
ta dog me			Это моя собака.
ta ke				Что это?
ca ta miau te		Это твоя кошка?
ca dom 'petia cun	Дом Пети высокий?
'piter sit			Питер сидит.
te no sit			Ты не сидишь.
me fi miau			Я люблю кошек.
te fi ke			Что ты любишь?
ca miau fi son		Кошки любят спать?
ca 'petia fu dog		Петя не любит собак?
'jeni go son		Дженни идёт спать.
'piter moi sit		Питер хочет сесть.
nui 'tom moi sit	Том тоже хочет сесть.
me ku go 'madrid	Я поеду в Мадрид.
me ki go 'istanbul	Я ездил в Стамбул.
he go				Иди!
he no bai ta		Не покупай это!
muta jo fos 'banan	У него есть четыре банана.
'alis pa 'london	Алис в Лондоне`
			.split('\n').map( function(line) {
				let s = line.split(/\t+/);
				res[s[0]] = s[1];
			});
			return res;
		},
	},
}
</script>

