<p class="text-center text-h3">FAQ</p>

<h3>1. Что такое Nao?</h3>
<p> Nao – это сконструированный международный язык. Его особенности – максимальная простота, логичность, свобода и выразительность. Это язык без исключений.  Это язык, который можно выучить за одну неделю. </p>
<h3>2. Зачем нужен Nao?</h3>
<p> Многие люди по всему миру тратят годы на изучение иностранных языков, в особенности английского. И несмотря на это, многие не достигают достаточно хорошего уровня владения языка, в то время как многие бросают изучение на самых ранних этапах. Причина кроется в чрезмерной сложности естественных языков. Естественные языки сложились стихийно, никто их не сидел и не продумывал. Если человечество хочет себе хороший язык, его нужно сконструировать точно так же, как оно изобрело и сконструировало многие другие вещи, ставшие частью нашей жизни. </p>

<p>Цель Nao – быть простым и одновременно мощным языком. Он стирает барьеры между культурами своей ясной структурой в словаре и грамматике, чтобы потратить меньше времени на изучение и при этом достичь такого уровня владения, которого бы невозможно было достичь при изучении другого языка. Nao также помогает увидеть мир таким, такой он есть, а не прятать истину за расплывчатыми словами, как это часто делается во многих языках.</p>
<h3>3. Что уникального в Nao? </h3>
<p> Nao обладает уникальной мощной системой логического конструирования слов. Она позволяет достичь лучшего уровня выразительности и ясности, и одновременно выучить язык быстрей, чем любой другой. </p>
