<h3>4. Какой алфавит использует Nao?</h3>
<p> Nao использует латинский алфавит: 5 гласных и 14 согласных (для передачи иностранных слов задействуются ещё 4 буквы). Каждой букве соответствует один звук. Все слова читаются как пишутся. 
Вы также можете посмотреть <router-link to="/textbook/phrases">фразы для начинающих</router-link>. </p>

<h3>5. Что насчёт грамматики Nao?</h3>
<p> В Nao нет склонений и спряжений. Все формы – правильные. Система времён богата, но не обязательна. По умолчанию, порядок слов строг, но богатая система маркировочных слов позволяет сделать любой порядок слов. </p>

<h3>6. Кто и когда создал Nao? </h3>
<p> Разработка языка началась в 2013 году полиглотом из России. Язык прошёл через много реформ и версий, пока не преобрёл свою современную форму и первых говорящих в 2017. </p>

<h3>7.  Основан ли Nao на каком-то другом языке? </h3>
<p> В целом нет. Несмотря на то, что большинство базовых слов (корней) взято из различных языков (в основном европейских), их сочетания не похожи на ни один из существующих языков, и более того, значения этих сочетаний определяются правилами, а не уравниванием их значений со словами из других языков. Всё это делает словарь Nao независимым, однозначным и культурно нейтральным. </p>
<h3>8. Сколько слов в Nao?  </h3>
<p> Бесконечно. Это аналогично вопросу: «Сколько предложений в русском языке?». Так же как грамматика русского языка позволяет генерировать бесконечное число предложений, грамматика Nao позволяет конструировать неограниченное число слов. Nao – агглютинативный язык, т.е. в нём слова склеиваются в сколь-угодно длинные цепочки. </p>
<h3>9. Могу ли я сам конструировать слова?  </h3>
<p> Конечно. Более того, все скомбинированные слова в словаре – это всего лишь рекомендации, они не имеют какого-то особого статуса. Так что ваши слова настолько же хороши как и те, которые вы видите в словаре. Главное – строго следовать правилам грамматики. </p>

<h3>10. Но язык – это живой организм. Не будет ли его логичность уничтожена по мере использования языка?
</h3>
<p> В нынешний век информации, когда распространено образование и средства массовой информации, поддерживать большие языки в стандартной форме стало гораздо проще. Nao также обладает своими уникальными свойствами, которые будут обеспечивать его стабильность: во-первых, любое изменение (семантическое или фонетическое) будет слишком заметно из-за своей нерегулярности, а так как слова в Nao сильно взаимосвязаны, то это вызовет цепную реакцию, изменяя огромное количество слов. Таким образом, любое изменение будет настолько большим, что просто не сможет прижиться. Во-вторых, Nao не является ничьим родным языком, это вспомогательный второй язык. По этой причине его опять же будет проще держать в стабильной форме. Можно представить его себе как версию языка программирования – она будет изменяться и улучшаться по мелочам (в основном добавляя что-то новое) в ответ на запрос общества. </p>


