<template><div>


<table v-if="line" class="mb-4 pl-sm-7">
	<tr v-for="k in keys">
		<td> <nao class="WRAP" :pronounce="!no_pronounce" :n="k"/> </td>
		<td class="pl-2"> {{ pairs[k] }} </td>
	</tr>
</table>


<v-flex v-else-if="little_columns" d-flex flex-wrap mb-4 pl-7>
	<v-flex v-for="p in little_columns" pa-2>
		<div v-for="w in Object.keys(p)">
			<nao :pronounce="!no_pronounce" :n="w"/>
			= <span> {{ p[w] }}</span>
		</div>
	</v-flex>
</v-flex>


<v-flex v-else d-flex flex-wrap px-7>
	<v-flex v-for="w in keys" mb-4 xs12 :sm6="keys.length != 1">
		<nao class="WRAP" :pronounce="!no_pronounce" :n="w"/>
		<div v-html="pairs[w]"> </div>
	</v-flex>
</v-flex>


</div></template>

<script>

/*

<v-container v-if="line" mb-4>
	<v-row justify="center" v-for="k in keys">
		<v-col class="col-3 d-flex justify-end">  <nao pronounce :n="k"/>  </v-col>
		<v-col class="d-flex align-center"> {{ pairs[k] }} </v-col>
	</v-row>
</v-container>

<v-flex v-else d-flex flex-wrap>
	<v-flex v-for="w in keys" mb-4 xs12 sm6 px-4>
		<nao pronounce :n="w"/>
		<div> {{ pairs[w] }} </div>
	</v-flex>
</v-flex>


*/

import nao from '@/components/Naoword'

export default {
	components: {nao},
	props: {
		pairs: Object,
		line: Boolean,
		little_columns: Array,
		no_pronounce: Boolean
	},
	data() {
		return {

		}
	},
	computed: {
		keys() {
			return Object.keys(this.pairs);
		}
	}
}
</script>

